@import url("https://fonts.googleapis.com/css?family=Raleway:400,500,600|Roboto:400,500");
.header {
  background-color: black;
  color: white; }
  .header .hero-body {
    padding: 2rem 1.5rem; }
  .header .logo {
    font-family: "Raleway", sans-serif;
    color: white;
    font-size: 32px;
    letter-spacing: 1px;
    font-weight: 600; }
    .header .logo:hover {
      color: tomato; }
  .header .header_links {
    color: tomato;
    font-weight: bold; }
  .header .header_links:hover {
    text-decoration: underline;
    color: inherit; }
  .header .info-container {
    min-height: 72px;
    font-size: 0.9rem; }



