.card {
  height: 100%;
  padding: 24px;
  cursor: pointer;
  transition: box-shadow 0.2s ease;
  border-radius: 4px; }
  .card:hover {
    box-shadow: lightcoral 0 0 8px 0;
    border-color: lightcoral; }
  .card:active {
    transform: scale(0.995); }



