@import url("https://cdnjs.cloudflare.com/ajax/libs/bulma/0.6.2/css/bulma.min.css");
@import url("https://fonts.googleapis.com/css?family=Raleway:400,500,600|Roboto:400,500");
html {
  font-size: 16px; }

body {
  font-family: "Roboto", sans-serif; }

h1, h2, h3, h4, h5 {
  font-family: "Raleway", sans-serif; }

strong {
  color: inherit; }

.centered-block {
  display: block;
  margin: 0 auto; }

.form_wrapper {
  max-width: 752px;
  margin: 36px auto;
  padding: 0 24px;
  text-align: center; }



